.fondo-semi-black{
 background-color: hsla(0, 0%, 21%, 0.5);
}

.footer {
 padding: 1.5rem 1rem 1.5rem;
}

.modal{
 z-index: 1024;
}

.modal-content{
 width: 90%;
}

.center-table th {
 text-align: center !important;
 vertical-align: middle;
}

html { overflow-y: auto; }

.leaflet-fade-anim .leaflet-tile,.leaflet-zoom-anim .leaflet-zoom-animated { will-change:auto !important; }

.hero-f404 {
 background-repeat: no-repeat;
 background-attachment: fixed;
 background-position: center;
 background-size: cover;
}

.hero-body .columns{
 width: 100%;
 align-self: stretch;
}

#datos-incendios {
 height:55vh;
 overflow-y: scroll;
}

.alineado-v{
 align-items: center;
}

.alineado-t{
 align-self: flex-start;
}

.media-left{
 flex-basis: 25%;
 overflow: auto;
}

@media screen and (max-width: 768px) {
 #datos-incendios {
 height:28vh;
 overflow-y: scroll;
 }

 .version-movil1 {
 height: 50vh;
 width: 95%;
 }
 .version-movil2 {
 height: 40vh;
 width: 95%;
 }
 .cm-modal{
 max-width: 80%;
 right: 10%;
 }

 article.media,div.box{
 padding: 0!important;
 }
 h1.title{
 margin-bottom: 0.5rem;
 }
 .footer {
 padding: 1rem 0.5rem 1rem;
 }
}
